.schedule-filter {
   height: 50px;
   // background-color: rgba(119, 136, 153, 0.1);
   display: flex;
   align-items: center;
   justify-content: space-between;
   position: fixed;
   top: 50px;
   width: 100%;
   gap: 20px;
   margin-top: 2px;
   margin-bottom: 2px;
   margin-left: 10px;
   margin-right: 10px;

   .board-container {
      display: flex;
      align-items: center;
      gap: 20px;

      .board {
         margin-left: 10px;
      }

      .add-board {
         border: 1px solid lightgray;
         border-radius: 4px;
         height: 35px;
         display: flex;
         align-items: center;
         padding: 10px;
         color: #000000de;

         &:hover {
            background-color: #ffff0a82;
            cursor: pointer;
            border: 1px solid #000000de;
         }
      }
   }

   .date {
      margin-right: 20px;
      ;
   }
}