.schedule-header {
   height: 50px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   position: fixed;
   top: 0px;
   width: 100%;
   border-bottom: 1px solid #1212123d;

   h1 {
      font-family: Livvic-Italic;
      font-size: 25px;
      margin-left: 10px;
   }

   .login {
      height: 36px;
      width: 36px;
      border-radius: 50%;
      background-color: lightpink;
      margin-right: 10px;

      svg {
         width: 36px;
         height: 34px;
      }
   }
}