.task-grid-template {
   cursor: pointer;
   border-radius: 5px;
   box-shadow: 0 0 1px 0px lightgrey;
   color: lightgray;
   font-size: 12px;
   padding-left: 2px;
   display: flex;
   align-items: center;

   &:hover {
      box-shadow: 0 0 1px 1px;
   }

   &.select-cell{
      background-color: lightgray;
      cursor: grabbing;
   }

}