.task-container {
   position: relative;
   margin: 1px;
   
   .task-preview {
      border-radius: 5px;
      font-size: 12px;
      cursor: pointer;
      resize: none;
      padding-left: 10px;
      font-family: Livvic-Regular;
      margin: 1px;
      border: unset;
      width: 100%;
      height: 100%;

      &:hover {
         box-shadow: 0 0 1px 1px;
      }

      &:focus {
         background-color: beige !important;
      }
   }

   .task-preview-btn {
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: 13px;
      border: unset;
      background-color: unset;
      font-family: Livvic-Regular;

      // &:hover {
      //    color: #121212;
      //    box-shadow: 0 0 2px 1px;
      //    border-radius: 3px;
      // }
   }
}